//转换中国标准时间转化成时间格式开始
function changTime(data){
	var datetime=data.getFullYear() + '-' + (data.getMonth() + 1) + '-' + (data.getDate()-1);
	  return datetime;
}
//转换中国标准时间转化成时间格式结束
//审批轨迹开始
function showAdvice(obj){
	
	if($(obj).next().css("display")=="block"){
		$(obj).next().slideUp();
		$(obj).children().eq(3).find("img").attr("src","resources/imgs/select-right-gray.jpg");
	}
	else{
		$(obj).next().slideDown();
		$(obj).children().eq(3).find("img").attr("src","resources/imgs/select-down-gray.jpg");
	}
}
//审批轨迹结束
